// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getSearchPodcast } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 零
      zero: 0,
      // 搜索的关键词
      searchKey: '',
      // 接收的电台
      djRadios: [],
      // html渲染的电台名
      vHtmlPodcast: '',
      // 是否显示v-html嵌入的电台名关键字html标签
      isVHtmlPodcast: 0,
      // 查到的电台数量
      djRadiosCount: 0
    };
  },

  async mounted() {
    // 获取关键词
    this.searchKey = store.state.searchKey; // 搜索电台
    // let res = await getSearchPodcast(this.searchKey)

    if (sessionStorage.getItem('searchKey')) {
      let res = await getSearchPodcast(sessionStorage.getItem('searchKey')); // console.log('搜索电台', res);

      this.djRadiosCount = res.data.result.djRadiosCount;
      this.djRadios = res.data.result.djRadios;
      if (this.djRadios.length !== 0) this.isLoading = 0; // console.log(this.djRadios);
    }
  },

  methods: {
    // 对播放量的处理
    changeCount(num) {
      return this.$formatCount(num);
    },

    // 电台名关键词高亮显示
    activePodcast(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlPodcast = 0;
        return string;
      } else {
        this.isVHtmlPodcast = 1;
        this.vHtmlPodcast = string;
        return '';
      }
    }

  }
};